<template>
  <div class="container mt-5 d-flex justify-content-center align-items-center flex-wrap">
      <div class="card m-3" style="width: 18rem;" v-for="product in products">
        <picture style="height:200px;overflow:hidden">
            <img class="card-img-top" :src="product.imagenes.imagen1" :alt="product.nombre">
        </picture>        
        <div class="card-body">
            <h5 class="card-title">{{product.nombre}}</h5>
            <p class="card-text">Precio: S/. {{product.precio - 0.1}}0 </p>
            <p class="card-text">Entrega: 7 a 10 das</p>
            <a href="#" class="btn btn-primary">COMPRAR</a>
        </div>
    </div>
  </div>
</template>
<script>
import {db} from '../firebase'
export default {
  data(){
      return{
          products:[]
      }
  },
  created(){
      db.ref('/productos').on('value',snapshot=>{
          this.products=[]
          for(let key in snapshot.val()){
              this.products.push({
                  id:key,
                  nombre:snapshot.val()[key].nombre,
                  marca:snapshot.val()[key].marca,
                  precio:snapshot.val()[key].precio,
                  imagenes:snapshot.val()[key].imagenes
              })
          }
      });
  }
}
</script>
<style>

</style>



