<template>
  <div class="container mt-5">
      <form @submit.prevent="Registrar()" class="col col-md-6 offset-3 bg-primary p-3 rounded">
          <h1 class="text-center font-weight-bold">REGISTRAR PRODUCTO</h1>
          <div class="form-group mt-4">
              <label for="" class="font-weight-bold">Nombre</label>
              <input type="text" class="form-control" v-model="nombre">
          </div>
          <div class="form-group">
              <label for="" class="font-weight-bold">Marca</label>
              <input type="text" class="form-control" v-model="marca">
          </div>
          <div class="form-group">
              <label for="" class="font-weight-bold">Precio</label>
              <input type="text" class="form-control" v-model="precio">
          </div>
          <div class="form-group">
              <label for="" class="font-weight-bold">Imagenes</label>
              <input type="text" class="form-control mb-2" v-model="imagenes.imagen1">
              <input type="text" class="form-control mb-2" v-model="imagenes.imagen2">
              <input type="text" class="form-control mb-2" v-model="imagenes.imagen3">
              <input type="text" class="form-control mb-2" v-model="imagenes.imagen4">
              <input type="text" class="form-control mb-2" v-model="imagenes.imagen5">
          </div>
          <button class="btn btn-success">AGREGAR</button>
      </form>
  </div>
</template>
<script>
import {db} from '../firebase'
export default {
  data(){
      return{
          nombre:'',
          marca:'',
          precio:'',
          imagenes:{}
      }
  },
  methods:{
      Registrar(){
          db.ref('/productos').push({
              nombre:this.nombre,
              marca:this.marca,
              precio:this.precio!=''? this.precio: 999,
              imagenes:this.imagenes
          })
          .then(()=>{
              this.nombre='',
              this.marca='',
              this.precio='',
              this.imagenes={}
          })
      }
  }
}
</script>
<style>

</style>



